import Vue from 'vue'
import App from './App'

/* eslint-disable no-new */
new Vue({
  el: 'body',
  components: {
    App
  },
  ready() {
    // API
    window.map = new BMap.Map('map') // Map
    map.centerAndZoom(new BMap.Point(116.404, 39.915), 11) // ,
    map.setCurrentCity('') //  

    setInterval(() => {
      let arr = document.getElementsByClassName('anchorBL')
      for (let item of arr) {
        item.style['z-index'] = 9
      }
    }, 2000)
  }
})



